@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body{font-family:"Open Sans";margin:20px;background-color:#E9E1CC}ol,ul{padding-left:30px}.board-row:after{clear:both;content:"";display:table}.status{margin-bottom:10px}.square{background:#E9E1CC;border:2px solid #EA9085;float:left;margin-right:-1px;margin-top:-1px;padding:20px;text-align:center;height:170px;width:170px}@media (max-width: 900px){.square{height:25vw;width:25vw;padding:0vw}.square svg{width:80%;height:80%}.square button{padding:0px}}.square button{font-size:24px;font-weight:bold;line-height:34px;background:#E9E1CC;border:none;width:100%;height:100%}.square button:hover{background-color:#DBD2BB;border-radius:20px}.square button:focus{outline:none}.game-board div .board-row:first-child .square{border-top:none}.game-board div .board-row:last-child .square{border-bottom:none}.game-board div .board-row .square:first-child{border-left:none}.game-board div .board-row .square:last-child{border-right:none}.square:focus{outline:none}.winning.O line,.winning.O circle{stroke:#D45079;-webkit-animation-name:O;animation-name:O;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes O{0%{stroke:#D45079}50%{stroke:#6E5773}100%{stroke:#D45079}}@keyframes O{0%{stroke:#D45079}50%{stroke:#6E5773}100%{stroke:#D45079}}.winning.X line,.winning.X circle{stroke:#2A7886;-webkit-animation-name:X;animation-name:X;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes X{0%{stroke:#2A7886}50%{stroke:#17434B}100%{stroke:#2A7886}}@keyframes X{0%{stroke:#2A7886}50%{stroke:#17434B}100%{stroke:#2A7886}}.kbd-navigation .square:focus{background:#ddd}.game{display:flex;flex-direction:row;justify-content:center}@media (max-width: 900px){.game{flex-direction:column}}.game-info{margin-left:50px}.game-info ol li{margin-bottom:5px}.game-info ol li:nth-child(odd){color:#2A7886}.game-info ol li:nth-child(even){color:#D45079}.game-info button{background:none !important;border:none;padding:0 !important;cursor:pointer;font-size:17px;margin-left:5px}.game-info button:focus{outline:none}

